// //--------------------------------------------------------------------------------------------------------------------------------------------------------------
// // Text inputs
// //--------------------------------------------------------------------------------------------------------------------------------------------------------------

input[type="text"],
input[type="email"],
input[type="date"],
.qld__text-input,
textarea {
  color: var(--QLD-color-light__text);
  @include QLD-space(padding, 0.75unit 1unit);
  border-radius: var(--QLD-border-radius-xs);
  border: var(--QLD-border-width-default) solid var(--QLD-color-light__border--alt);
  background-color: var(--QLD-color-light__background);
  box-sizing: border-box;
  appearance: none;
  max-width: 46rem;

  @include QLD-focus();

  &::placeholder {
    color: var(--QLD-color-light__text--lighter);
  }

  // &.qld__input--valid {
  // 	&:not(:focus) {
  // 		border-color: var(--QLD-color-status__success;
  // 		background-color: var(--QLD-color-status__success--lightest; //var(--QLD-color-status__success with .05 opacity
  // 	}

  // }

  &.qld__text-input--valid {
    color: var(--QLD-color-light__text);
    border-color: var(--QLD-color-status__success);
    background-color: var(--QLD-color-status__success--lightest);
    &:hover:not(:focus) {
      background-color: var(--QLD-color-status__success--lighter);
      border-color: var(--QLD-color-status__success);
      color: var(--QLD-color-light__text);
    }

    &:focus {
      background-color: var(--QLD-color-light__background);
      border-color: var(--QLD-color-neutral--lighter);
    }
  }

  &.qld__input--error {
    border-color: var(--QLD-color-status__error);
    background-color: RGBA(226, 35, 57, 0.05); //var(--QLD-color-status__error) with .05 opacity
  }

  .qld__input--success + &,
  .qld__input--error + & {
    @include QLD-space(margin-top, 0.5unit);
    display: block;
  }

  &.qld__text-input--error {
    &:not(:focus) {
      border-color: var(--QLD-color-status__error);
      background-color: var(--QLD-color-status__error--lightest);
    }

    &:hover:not(:focus) {
      border-color: var(--QLD-color-status__error);
      background-color: var(--QLD-color-status__error--lighter);
    }
  }

  &:hover {
    &:not(:focus):not(.qld__text-input--error):not(.qld__text-input--valid) {
      background-color: var(--QLD-color-light__background--shade);
      border-color: var(--QLD-color-light__action--primary-hover);
    }
  }

  // disabled styling
  &[disabled],
  &:disabled {
    cursor: not-allowed;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=50)";
    opacity: 0.5;
    &:hover,
    &:hover:not(.qld__text-input--error):not(.qld__text-input--valid) {
      background-color: transparent;
      border-color: var(--QLD-color-light__border--alt);
    }
  }

  &[aria-expanded="true"],
  &:hover[aria-expanded="true"] {
    background-color: var(--QLD-color-light__background);
  }

  &:focus {
    background-color: var(--QLD-color-light__background);
    border-radius: var(--QLD-border-radius-xs);
    border: var(--QLD-border-width-default) solid var(--QLD-color-light__border);
    &::placeholder {
      color: var(--QLD-color-light__text--lighter);
    }

    &::selection {
      background-color: var(--QLD-color-dark__background);
      color: var(--QLD-color-dark__text);
    }
  }

  .qld__body--dark &,
  .qld__body--dark-alt &,
  .qld__header__main--dark &,
  .qld__header__main--dark-alt &,
  .qld__banner--dark &,
  .qld__banner--dark-alt & {
    color: var(--QLD-color-dark__text--lighter);
    border-color: var(--QLD-color-dark__border--alt);
    background-color: var(--QLD-color-dark__background);

    &::placeholder {
      color: var(--QLD-color-dark__text--lighter);
    }

    &:focus {
      background-color: var(--QLD-color-light__background);
      border-color: var(--QLD-color-neutral--lighter);
      &::placeholder {
        color: var(--QLD-color-light__text--lighter);
      }

      &::selection {
        background-color: var(--QLD-color-dark__background);
        color: var(--QLD-color-dark__text);
      }
    }

    @include QLD-focus(dark);

    &:focus,
    &:active {
      background-color: var(--QLD-color-light__background);
      color: var(--QLD-color-light__text);
    }

    &.qld__input--valid {
      color: var(--QLD-color-light__text);
    }

    &.qld__input--error {
      border-color: var(--QLD-color-status__error);
      background-color: RGBA(226, 35, 57, 0.05); //var(--QLD-color-status__error) with .05 opacity
    }

    &.qld__text-input--valid {
      color: var(--QLD-color-light__text);
      border-color: var(--QLD-color-status__success);
      background-color: var(--QLD-color-status__success--lightest);
      &:hover:not(:focus) {
        background-color: var(--QLD-color-status__success--lighter);
        border-color: var(--QLD-color-status__success);
        color: var(--QLD-color-light__text);
      }

      &:focus {
        background-color: var(--QLD-color-light__background);
        border-color: var(--QLD-color-neutral--lighter);
      }

      &::placeholder {
        color: var(--QLD-color-light__text--lighter);
      }
    }

    &.qld__text-input--error {
      &:hover:not(:focus) {
        border-color: var(--QLD-color-status__error);
        background-color: var(--QLD-color-status__error--lighter);
        color: var(--QLD-color-light__text);
      }

      &::placeholder {
        color: var(--QLD-color-light__text--lighter);
      }
      color: var(--QLD-color-light__text);
    }

    &:hover {
      &:not(:focus):not(.qld__text-input--error):not(.qld__text-input--valid) {
        color: var(--QLD-color-dark__text);
        background-color: var(--QLD-color-neutral--black__opacity-10);
        border-color: var(--QLD-color-dark__action--primary-hover);
      }
    }

    &[disabled],
    &:disabled {
      cursor: not-allowed;
      -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=50)";
      opacity: 0.5;
      &:hover,
      &:hover:not(.qld__text-input--error):not(.qld__text-input--valid) {
        background-color: transparent;
        border-color: var(--QLD-color-dark__border--alt);
      }
    }

    &[aria-expanded="true"],
    &:hover[aria-expanded="true"] {
      color: var(--QLD-color-light__text);
      background-color: var(--QLD-color-light__background);
    }
  }
}

// Honeypot field
.qld__text-input--validation {
  display: none;
}

.qld__form-style-filled {
  .sq-form-question input,
  .sq-form-question textarea,
  .sq-form-question-email-address input,
  .qld__text-input {
    background-color: var(--QLD-color-light__background--shade);
    border-width: 0 0 2px 0;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    @include QLD-space(padding, 0.875unit 1.125unit 0.75unit 1.125unit);

    &:hover {
      &:not(:focus) {
        background-color: var(--QLD-color-light__background--shade);
        border-color: var(--QLD-color-light__action--primary-hover);
        cursor: text;
      }
    }

    &[aria-expanded="true"],
    &:hover[aria-expanded="true"] {
      background-color: var(--QLD-color-light__background--shade);
      border-color: transparent;
    }

    &:focus {
      background-color: var(--QLD-color-light__background);
      border: var(--QLD-border-width-default) solid var(--QLD-color-light__border);
      border-bottom-left-radius: 4px;
      border-bottom-right-radius: 4px;
      @include QLD-space(padding, 0.75unit 1unit 0.75unit 1unit);
    }

    &.qld__text-input--dark.qld__input--error,
    &.qld__input--error,
    &--invalid {
      background-color: var(--QLD-color-status__error--lightest);
      border-color: var(--QLD-color-status__error);

      &:hover {
        background-color: var(--QLD-color-status__error--lighter);
      }

      &:focus {
        background-color: var(--QLD-color-light__background);
        border-color: transparent;
      }
    }

    &.qld__text-input--dark.qld__input--valid,
    &.qld__input--valid,
    &--valid {
      background-color: var(--QLD-color-status__success--lightest);
      border-color: var(--QLD-color-status__success);
      color: var(--QLD-color-light__text);

      &:hover {
        &:not(:focus) {
          background-color: var(--QLD-color-status__success--lighter);
          border-color: var(--QLD-color-status__success);
        }
      }
    }

    &[disabled],
    &:disabled {
      -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=50)";
      opacity: 0.5;
      &:hover,
      &:hover:not(.qld__text-input--error):not(.qld__text-input--valid) {
        cursor: not-allowed;
        background-color: var(--QLD-color-light__background--shade);
        border-color: var(--QLD-color-light__border--alt);
      }
    }
  }

  .qld__body--dark,
  .qld__body--dark &,
  .qld__body--dark-alt,
  .qld__body--dark-alt &,
  .qld__header__main--dark,
  .qld__header__main--dark-alt,
  .qld__banner--dark,
  .qld__banner--dark {
    .sq-form-question input,
    .sq-form-question textarea,
    .sq-form-question-email-address input,
    .qld__text-input {
      background-color: var(--QLD-color-dark__background--shade);
      border-color: var(--QLD-color-dark__border--alt);
      background-blend-mode: multiply;
      color: var(--QLD-color-dark__text);

      &::placeholder {
        color: var(--QLD-color-dark__text--lighter);
      }

      &:hover {
        &:not(:focus) {
          background-color: var(--QLD-color-dark__background--shade);
          border-color: var(--QLD-color-dark__action--primary);
        }
      }

      &:focus {
        background-color: var(--QLD-color-light__background);
        color: var(--QLD-color-light__text);
        border-color: var(--QLD-color-neutral--lighter);
        &::placeholder {
          color: var(--QLD-color-light__text--lighter);
        }
      }

      &[aria-expanded="true"],
      &:hover[aria-expanded="true"] {
        color: var(--QLD-color-light__text);
        background-color: var(--QLD-color-light__background);
      }

      &.qld__text-input--dark.qld__input--error,
      &.qld__input--error,
      &--invalid {
        background-color: var(--QLD-color-status__error--lightest);
        border-color: var(--QLD-color-status__error);
        color: var(--QLD-color-light__text);

        &::placeholder {
          color: var(--QLD-color-light__text--lighter);
        }

        &:hover {
          background-color: var(--QLD-color-status__error--lighter);
        }

        &:focus {
          border-color: transparent;
        }
      }

      &.qld__text-input--dark.qld__input--valid,
      &.qld__input--valid,
      &--valid {
        background-color: var(--QLD-color-status__success--lightest);
        border-color: var(--QLD-color-status__success);
        color: var(--QLD-color-light__text--lighter);

        &:hover {
          &:not(:focus) {
            background-color: var(--QLD-color-status__success--lighter);
          }
        }

        &:focus {
          border-color: transparent;
        }

        &::placeholder {
          color: var(--QLD-color-light__text--lighter);
        }
      }

      &.qld__text-input--dark.qld__input--error,
      &.qld__input--error,
      &--invalid,
      &.qld__text-input--dark.qld__input--valid,
      &.qld__input--valid,
      &--valid {
        &:focus {
          background-color: var(--QLD-color-light__background);
        }
      }

      &[disabled],
      &:disabled {
        -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=50)";
        opacity: 0.5;
        &:hover,
        &:hover:not(.qld__text-input--error):not(.qld__text-input--valid) {
          cursor: not-allowed;
          background-color: var(--QLD-color-dark__background--shade);
          border-color: var(--QLD-color-dark__border--alt);
        }
      }
    }
  }
}

.sq-form-question input,
.sq-form-question textarea,
.sq-form-question-email-address input,
.qld__text-input,
select,
.qld__select {
  &.qld__text-input--block {
    display: block;
    width: 100%;
    max-width: 100%;
    box-sizing: border-box;
  }

  &.qld__text-input--number {
    font-variant: tabular-nums;
    letter-spacing: 2px;
  }
}

