// Print styles
@media print {
  .sq-form-question input,
  .sq-form-question-email-address input,
  .qld__text-input {
    border-color: #000 !important;

    &[disabled] {
      color: var(--QLD-color-light__text--lighter) !important;
      background-color: #ccc !important;
      border: 2px solid #ccc !important;
    }

    &:disabled {
      color: var(--QLD-color-light__text--lighter) !important;
      background-color: #ccc !important;
      border: 2px solid #ccc !important;
    }
  }
}
